.wp-block-wordpress-search-metadata-filter {
    margin: 1rem 0;

    .search-metadata-filter__container {
        display: block;
    }

    .metadata-filter-form {
        width: 100%;
    }

    .metadata-filter {
        border: 1px solid #ddd;
        border-radius: 6px;
        padding: 1rem;
        background: #f9f9f9;
    }

    .metadata-filter__label {
        font-weight: 600;
        color: #333;
        margin: 0 0 1rem 0;
        font-size: 1rem;
        display: block;
    }

    .metadata-filter__options {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
        gap: 0.5rem;
        margin-bottom: 1rem;
    }

    .metadata-filter__option {
        display: flex;
        align-items: center;
        gap: 0.5rem;
        padding: 0.5rem;
        background: white;
        border: 1px solid #e0e0e0;
        border-radius: 4px;
        cursor: pointer;
        transition: all 0.2s ease;

        &:hover {
            background: #f5f5f5;
            border-color: #ccc;
        }

        &:has(.metadata-filter__checkbox:checked) {
            background: #e6f3ff;
            border-color: #007cba;
        }
    }

    .metadata-filter__checkbox {
        margin: 0;
        accent-color: #007cba;
    }

    .metadata-filter__option-label {
        font-size: 0.875rem;
        color: #333;
        flex: 1;
        line-height: 1.4;
    }

    .metadata-filter__actions {
        display: flex;
        gap: 0.75rem;
        align-items: center;
        flex-wrap: wrap;
        margin-top: 1rem;
        padding-top: 1rem;
        border-top: 1px solid #e0e0e0;
    }

    .metadata-filter__apply-button {
        background: #007cba;
        color: white;
        border: 1px solid #005a87;
        padding: 0.5rem 1rem;
        border-radius: 4px;
        font-size: 0.875rem;
        cursor: pointer;
        transition: all 0.2s ease;

        &:hover {
            background: #005a87;
            border-color: #004768;
        }
    }

    .metadata-filter__clear-button {
        background: #f0f0f0;
        color: #666;
        border: 1px solid #ccc;
        padding: 0.5rem 1rem;
        border-radius: 4px;
        font-size: 0.875rem;
        text-decoration: none;
        transition: all 0.2s ease;

        &:hover {
            background: #e0e0e0;
            border-color: #999;
            text-decoration: none;
        }
    }

    // Mobile responsive
    @media (max-width: 768px) {
        .metadata-filter__options {
            grid-template-columns: 1fr;
        }
        
        .metadata-filter__actions {
            flex-direction: column;
            align-items: stretch;
            
            .metadata-filter__apply-button,
            .metadata-filter__clear-button {
                text-align: center;
            }
        }
    }
}
